<template>
  <!-- <switch></switch> -->
  <!-- 侧边栏 -->

  <el-container style="height: 660.6px" class="container">
    <el-container>
      <!-- MarkDown笔记 -->
      <mavon-editor
        v-model="sumbitBlogContent"
        :ishljs="true"
        class="mEditor"
        fontSize="18px"
        @save="showDialog"
        

      >
        <!-- 刷新界面按钮 -->
        <button
          type="button"
          class="op-icon el-icon-refresh-right"
          :title="'刷新界面'"
          slot="left-toolbar-after"
          @click="loadBlog"
        ></button>
      </mavon-editor>
    </el-container>
    <!-- 右侧博客 -->
    <el-aside style="margin-right: 10px; overflow: hidden">
      <!-- 面包屑导航 -->
      <div style="margin: 7px 10px">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/jotter' }"
            >写文章</el-breadcrumb-item
          >
          <el-breadcrumb-item :to="{ path: '/blog' }"
            >文章详情</el-breadcrumb-item
          >
        </el-breadcrumb>
      </div>
      <!-- 文章概览/列表 -->
      <el-scrollbar>
        <div v-for="blog in blogs" :key="blog.id">
          <button
            style="background-color: transparent; border: none; width: 300.2px"
            @click="toBlog(blog)"
          >
            <el-card
              style="height: 180px; margin: 7px"
              body-style="padding: 7px"
            >
              <p class="blog_title">{{ blog.blogTitle }}</p>
              <p class="blog_Abs" align='left'>{{ blog.blogAbs }}</p>
              <!-- 删除文章按钮 -->
              <!-- <el-button
                icon="el-icon-delete"
                circle
                size="mini"
                style="left: 2px"
              ></el-button> -->
            </el-card>
          </button>
        </div>
      </el-scrollbar>
    </el-aside>
    <!-- 弹出框 -->
    <el-dialog
      title="博客信息"
      :visible.sync="dialogFormVisible"
      width="30%"
      center
    >
      博客标题
      <el-input
        class="el-input"
        v-model="sumbitBlogTitle"
        clearable
        size="small"
      ></el-input>
      博客简介
      <el-input class="el-input" type="textarea" v-model="sumbitBlogAbs">
      </el-input>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="blogSumbit">上 传</el-button>
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
export default {
  name: "JotterIndex",
  data() {
    return {
      //弹窗显隐控制
      dialogTableVisible: false,
      dialogFormVisible: false,
      //博客数据
      sumbitBlogTitle: "",
      sumbitBlogAbs: "",
      sumbitBlogContent: "",
      sumbitBlogMarkDown: "",
      sumbitBlogHtml: "",
      sumbitBlogDate: "",
      //当前用户名
      username: JSON.parse(window.localStorage.getItem("user" || "[]"))
        .username,
      //博客预览的数据
      blogs: [],
    };
  },
  mounted: function () {
    this.loadBlog();
  },
  methods: {
    //展现表单
    showDialog(value, render) {
      console.log(this.sumbitBlogContent, "@@", value, "@@", render);
      this.sumbitBlogMarkDown = value;
      this.sumbitBlogHtml = render;
      this.dialogFormVisible = true;
    },
    //将博客存入数据库
    blogSumbit() {
      var timeElapsed = Date.now();
      var today = new Date(timeElapsed);
      this.sumbitBlogDate = today.toLocaleDateString();
      console.log(this.sumbitBlogDate);

      this.$axios
        .post("http://localhost:3001/api/sumbitJotter", {
          sumbitBlogTitle: this.sumbitBlogTitle,
          sumbitBlogAbs: this.sumbitBlogAbs,
          sumbitBlogContent: this.sumbitBlogContent,
          sumbitBlogMarkDown: this.sumbitBlogMarkDown,
          sumbitBlogHtml: this.sumbitBlogHtml,
          sumbitBlogDate: this.sumbitBlogDate,
          username: this.username,
        })
        .then((resp) => {
          if (resp) {
            this.dialogFormVisible = false;
            this.$message({
              message: "上传成功",
              type: "success",
            });
            this.loadBlog();
          }
        });
    },
    //重载界面
    loadBlog() {
      var _this = this;
      this.$axios
        .post("http://localhost:3001/api/loadBlog", { username: this.username })
        .then((resp) => {
          if (resp) {
            console.log(resp.data);
            _this.blogs = resp.data;
          }
        });
    },
    //删除博客
    deleteBlog(bolg_id) {
      this.$axios
        .post("http://localhost:3001/api/deleteBlog", { id: bolg_id })
        .then((resp) => {
          if (resp) {
            this.$message({
              message: "删除成功",
              type: "success",
            });
            this.loadBlog();
          }
        });
    },
    toBlog(blog) {
      console.log(blog);
      this.$router.push({ path: "/blog", query: { blog: blog } });
    },
  },
};
</script>

<style scoped>
.mEditor {
  width: 100%;
  height: 653px;
  /* height: 100%; */
  margin-top: 7px;
  
}
/* .container >>> .auto-textarea-input{
  background-color: rgb(54, 59, 64);
  color: rgb(221, 221, 221);
}
.container >>> .v-note-edit{
background-color: rgb(54, 59, 64);
} */

.blog_title {
  font-size: 20px;
  font-weight: bold;
  margin: 3px;
}
.blog_Abs {
  font-size: 14px;
  display: inline-block;
  
}
.el-input {
  margin: 5px;
}
.el-scrollbar {
  text-align: center;
  height: 100%;
  overflow-x: hidden;
}
.deleteButton {
  size: 15px;
}
</style>


